<template>
  <div>
    <cms-breadcrumb to="/afterSales/list" type="list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="after-contanier" v-loading="loading">
      <!-- 退款退货详情 -->
      <div class="after-order-wrap" v-if="!loading">
        <div class="after-flow-left">
          <p>
            <span class="after-top-infos">售后服务单：</span>
            {{tableData.orderRefund.orderRefundNum}}
          </p>
          <p>
            <span class="after-top-infos">订单号：</span>
            {{tableData.order.orderCode}}
          </p>
          <p class="after-after-status">{{tableData.orderRefund.status| refundStatusFilter}}</p>
          <div v-if="tableData.lastIntervent">
            <el-button
              size="mini"
              type="primary"
              style="width:120px;margin-bottom:10px;"
              @click="checkService(tableData,true)"
              v-if="tableData.lastIntervent.platformStatus==0"
            >确认</el-button>
            <br>
            <el-button
              size="mini"
              type="primary"
              style="width:120px;margin-bottom:10px;"
              @click="checkService(tableData,false)"
              v-if="tableData.lastIntervent.platformStatus==0"
            >确认不通过</el-button>
            <br>
          </div>
        </div>
        <div class="after-flow-right">
          <!-- 流程部分 -->
          <div class="after-process-wrap">
            <div class="after-process-item"  v-if="item.operateType!==4"
            v-for="(item,index) in tableData.orderRefundDetails" :key="index">
                <div class="after-processlogo-wrap">
                <i class="el-icon-success after-process-logo"></i>
              </div>
              <div class="after-process-line" 
              v-if="index!==tableData.orderRefundDetails.length-1||tableData.orderRefund.status<8"
              ></div>
              <div class="after-process-info">
                <span class="after-process-title">{{operateFilter(item.operateType)}}</span> 
                <span v-if="true">{{$moment(item.createTime)}}</span>
              </div>
            </div>
            <div v-if="tableData.orderRefund.status==0" class="after-process-item after-processitem-dark">
              <div class="after-processlogo-wrap">
                <i class="el-icon-success after-process-logo after-processlogo-dark"></i>
              </div>
              <div class="after-process-line after-processline-dark"></div>
              <div class="after-process-info">
                <span class="after-process-title">卖家确认</span>
              </div>
            </div>
            <div v-if="tableData.orderRefund.status<8" class="after-process-item after-processitem-dark">
              <div class="after-processlogo-wrap">
                <i class="el-icon-success after-process-logo after-processlogo-dark"></i>
              </div>
              <div class="after-process-info">
                <span class="after-process-title">完成</span>
              </div>
            </div>
          </div>
          <!-- 物流部分 -->
          <div class="after-flow-main">
            <div
              class="after-flow-item"
              v-for="(item,index) in refundDetails"
              :key="index"
            >
              <div class="after-flow-itemmain">
                <div>
                  <img src="@/assets/images/circle.png" width="14" height="14" v-if="index!=0">
                  <img
                    src="@/assets/images/dot-circle-o.png"
                    width="14"
                    height="14"
                    v-if="index==0"
                  >
                </div>
                <span class="after-flow-time">{{$moment(item.createTime)}}</span>
                <span
                  class="after-flow-position"
                  :class="{'after-flow-bold':index==0?true:false}"
                >{{item.operateTxt}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 下頭部分 -->
      <div class="after-info-wrap" v-if="!loading">
        <div class="after-info-left">
          <p class="after-info-title">售后服务单信息</p>
          <div class="after-info-desc">
            <p class="after-info-para">
              <span class="after-info-tips">申请人：</span>
              <span
                class="after-info-concreat"
                v-if="tableData.orderRefund.member.nickname"
              >{{tableData.orderRefund.member.nickname}}({{tableData.orderRefund.member.username}} | {{tableData.orderRefund.member.phone}})</span>
              <span
                class="after-info-concreat"
                v-if="!tableData.orderRefund.member.nickname"
              >{{tableData.orderRefund.member.username}} | {{tableData.orderRefund.member.phone}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">申请时间：</span>
              <span class="after-info-concreat">{{$moment(tableData.orderRefund.applyTime)}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">货物状态：</span>
              <span class="after-info-concreat" v-if="tableData.orderRefund.productStatus==1">未收到货</span>
              <span class="after-info-concreat" v-if="tableData.orderRefund.productStatus==2">已收到货</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">服务类型：</span>
              <span
                class="after-info-concreat"
              >{{tableData.orderRefund.refundType | refundTypeFilter}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">原因类型：</span>
              <span class="after-info-concreat">{{tableData.orderRefund.refundReason}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">问题描述：</span>
              <span
                class="after-info-concreat"
                v-if="!loading"
              >{{tableData.orderRefund.customerTxt}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">凭证：</span>
              <span class="after-info-concreat">
                <img
                  v-for="(imgs,index) in tableData.orderRefund.pics"
                  :key="index"
                  :src="$cms.filterUrl(imgs.picResource.url)"
                  alt="凭证"
                  width="60"
                  height="60"
                  style="margin-right:10px;"
                >
              </span>
            </p>
          </div>
        </div>
        <div class="after-info-center">
          <div class="after-info-centerall">
            <p class="after-info-title">买家发货物流信息</p>
            <div v-if="tableData.orderRefund.customerExpressNum">
              <p class="after-info-para">
              <span class="after-info-tips">配送地址：</span>
              <span
                class="after-info-concreat"
                v-if="tableData.orderRefund.sellerAddress"
              >{{tableData.orderRefund.sellerAddress}}</span>
              <span class="after-info-concreat" v-if="!tableData.orderRefund.sellerAddress">-</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips"></span>
              <span class="after-info-concreat">{{tableData.orderRefund.sellerName}} （ {{tableData.orderRefund.sellerTel}} ）</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">运送方式：</span>
              <span
                class="after-info-concreat"
                v-if="tableData.orderRefund.customerExpressCompany"
              >{{tableData.orderRefund.customerExpressCompany.cnName}}</span>
              <span
                class="after-info-concreat"
                v-if="!tableData.orderRefund.customerExpressCompany"
              >-</span>
            </p>
            <div class="after-info-para">
              <span class="after-info-tips">运单号：</span>
              <span
                class="after-info-concreat"
               
              >{{tableData.orderRefund.customerExpressNum}}</span>
              <span class="after-info-concreat" v-if="!tableData.orderRefund.customerExpressNum">-</span>
              <el-popover placement="top" width="700" trigger="click">
                <ul class="after-flow__items">
                  <li v-for="(item,index) in buyerFlowList" :key="index" class="after-flow__item">
                    <span class="after-flow__time">
                      <i class="el-icon-time"></i>
                      {{item.acceptTime}}
                    </span>
                    <span class="after-flow__content">{{item.acceptStation}}</span>
                  </li>
                </ul>
                <span slot="reference" class="after-info-viewdes" >查看更多</span>
              </el-popover>
            </div>
            </div>
          </div>
        </div>
        <div class="after-info-right">
          <p class="after-info-title">平台审核结果</p>
          <div class="after-audit-wrap">
            <div class="after-audit-logo">
              <i class="el-icon-warning"></i>
            </div>
            <div class="after-audit-info">
              如果买家于卖家就售后服务单协商未果，导致卖家未确认售后服务单相关信息，
              平台将作为协商方介入，最终会由平台确认结果
            </div>
          </div>
          <div
            class="after-audit-result"
            v-if="tableData.orderRefund.platformIntervent&&tableData.lastIntervent"
          >
            <p class="after-info-para">
              <span class="after-info-tips">审核时间：</span>
              <span
                class="after-info-concreat"
                v-if="tableData.lastIntervent"
              >{{$moment(tableData.lastIntervent.createTime)}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">审核结果：</span>
              <span
                class="after-info-concreat"
              >{{tableData.lastIntervent.platformStatus|platformStatusFilter}}</span>
            </p>
            <p class="after-info-para">
              <span class="after-info-tips">备注：</span>
              <span class="after-info-concreat">{{tableData.lastIntervent.interventResult}}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="desc-table-wrap" v-if="!loading">
      <!-- 数据列表 -->
      <table class="order-table">
        <tr>
          <th>商品</th>
          <th>单价</th>
          <th>数量</th>
          <th>退款明细</th>
        </tr>
        <tbody>
          <tr>
            <td class="product-info">
              <div class="product__inner">
                <img
                  class="product-img"
                  :src="$cms.filterUrl(tableData.product.coverImage)"
                  v-if="tableData.product"
                >
                <div class="product-title" v-if="tableData.product.name">
                  <h3 class="title-h3">{{tableData.product.name}}</h3>
                  <p
                    class="title-p"
                    style="margin-top:10px;"
                    v-if="tableData.proSkuAttrList"
                  >{{showAttr(tableData.proSkuAttrList)}}</p>
                </div>
              </div>
            </td>
            <td class="order-count">
              <span>￥{{$cms.float(tableData.orderItem.productPrice)}}</span>
            </td>
            <td class="order-count">x{{tableData.orderItem.productCount}}</td>
            <td class="order-count">
              <div>
                <span class="order-count-named">积分:</span>
                <span
                  class="order-count-value"
                  v-if="tableData.orderRefund.refundIntegral"
                >{{tableData.orderRefund.refundIntegral}}</span>
                <span class="order-count-value" v-if="!tableData.orderRefund.refundIntegral">0</span>
              </div>
              <div>
                <span class="order-count-named">红包:</span>
                <span
                  class="order-count-value"
                  v-if="tableData.orderRefund.refundRedPacket"
                >{{$cms.float(tableData.orderRefund.refundRedPacket)}}</span>
                <span class="order-count-value" v-if="!tableData.orderRefund.refundRedPacket">￥0.00</span>
              </div>
              <div>
                <span class="order-count-named">退款金额:</span>
                <span class="order-count-value">￥{{$cms.float(tableData.orderRefund.refundAmount)}}</span>
              </div>
              <div>
                <span class="order-count-named">退款方式:</span>
                <span class="order-count-value">{{tableData.order.payMode|payModeFilter}}返还</span>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <check-service ref="checkService" @change="changeService"></check-service>
  </div>
</template>

<script>
import mixin from './mixin'
import checkService from './checkService'
export default {
  mixins: [mixin],
  components: {
    checkService
  },
  data() {
    return {}
  },
  methods: {}
}
</script>

<style scoped>
.after-info-left {
  width: 29.9%;
}
.after-info-center {
  width: 35%;
  border-right: 1px solid #ebeef5;
}
.after-info-right {
  width: 35%;
}
.after-audit-wrap {
  max-width: 380px;
}
.after-audit-info {
  max-width: 260px;
}
.after-info-concreat {
  max-width: 220px;
}
.order-count-named {
  display: inline-block;
  width: 70px;
  text-align: right;
  margin-right: 10px;
}
.order-count-value {
  display: inline-block;
  min-width: 80px;
  text-align: left;
  color: red;
}
.after-flow-line {
  position: absolute;
  top: -23px;
}
</style>



